<template>
  <div>
    <div>공모전/대회</div>
    <b-table striped hover :items="posts"></b-table>
  </div>
</template>

<script>
import PostService from '../PostService';

  export default {
    name : 'Board',
    data() {
      return {
        items: [],
        error: ""
      }
    },
    async created() {
      try {
        this.posts = await PostService.getPosts();
      }catch(err){
        this.error = err.message;
      }
    }
  }
</script>